"use client";

import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { createOrder, getNextOrderNo } from "../api"; // make sure getNextOrderNo exists

interface AddOrderDialogProps {
  open: boolean;
  onClose: () => void;
  refreshData: () => void;
}

export function AddOrderDialog({ open, onClose, refreshData }: AddOrderDialogProps) {
  const [orderNo, setOrderNo] = useState(""); // serial order number
  const [customerName, setCustomerName] = useState("");
  const [amount, setAmount] = useState("");
  const [status, setStatus] = useState("OPEN");
  const [loading, setLoading] = useState(false);

  // Fetch next order number when dialog opens
  useEffect(() => {
    if (open) {
      getNextOrderNo()
        .then((nextNo) => setOrderNo(nextNo))
        .catch(() => setOrderNo("ORD-???"));
    }
  }, [open]);

  const handleSave = async () => {
    if (!customerName || !amount) {
      toast.error("Please enter all fields");
      return;
    }

    try {
      setLoading(true);

      await createOrder({
        orderNo, // backend-generated serial number
        customerName,
        amount: parseFloat(amount),
        status,
      });

      toast.success(`Order ${orderNo} created successfully`);
      setCustomerName("");
      setAmount("");
      setStatus("OPEN");
      onClose();
      refreshData();
    } catch (err) {
      toast.error("Failed to create order");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Add Order</DialogTitle>
        </DialogHeader>

        <div className="flex flex-col gap-2 mt-2">
          {/* Read-only Order No */}
          <Input value={orderNo} readOnly placeholder="Order No" />

          {/* Customer Name */}
          <Input
            placeholder="Customer Name"
            value={customerName}
            onChange={(e) => setCustomerName(e.target.value)}
          />

          {/* Amount */}
          <Input
            placeholder="Amount"
            type="number"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
          />

          {/* Status */}
          <select
            className="border rounded-md p-2"
            value={status}
            onChange={(e) => setStatus(e.target.value)}
          >
            <option value="OPEN">OPEN</option>
            <option value="PENDING">PENDING</option>
            <option value="INPROGRESS">INPROGRESS</option>
            <option value="COMPLETED">COMPLETED</option>
            <option value="CANCELLED">CANCELLED</option>
          </select>
        </div>

        {/* Save Button */}
        <Button
          className="w-full mt-4"
          onClick={handleSave}
          disabled={loading || !orderNo}
        >
          {loading ? "Saving..." : "Save"}
        </Button>
      </DialogContent>
    </Dialog>
  );
}
