"use client";

import React, { useEffect, useState } from "react";
import { OrdersTable } from "./orders/components/orders-table";
import { getOrders } from "./orders/api";
import { Order } from "./orders/types";
import { toast } from "sonner";
import { getColumns } from "./orders/columns";
import { AddOrderDialog } from "./orders/components/add-order-dialog";
import { Button } from "@/components/ui/button";

export default function OrdersPage() {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [pageSize] = useState(10);
  const [total, setTotal] = useState(0);
  const [addDialogOpen, setAddDialogOpen] = useState(false);

  // Fetch orders from backend
  const fetchOrders = async (pageNumber = page) => {
    setLoading(true);
    try {
      const res = await getOrders(pageNumber, pageSize);
      setOrders(res.data.items);
      setTotal(res.data.total);
      setPage(res.data.page);
    } catch {
      toast.error("Failed to fetch orders");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchOrders(page);
  }, [page]);

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Orders</h1>

      {/* Add Order Button */}
      <Button className="mb-4" onClick={() => setAddDialogOpen(true)}>
        Add Order
      </Button>

      {/* Add Order Dialog */}
      <AddOrderDialog
        open={addDialogOpen}
        onClose={() => setAddDialogOpen(false)}
        refreshData={fetchOrders} // Refresh table after adding
      />

      {/* Orders Table */}
      <OrdersTable
        data={orders}
        page={page}
        pageSize={pageSize}
        total={total}
        loading={loading}
        onPageChange={setPage}
        refreshData={fetchOrders}
        columns={getColumns(fetchOrders)} // Pass columns with Delete
      />
    </div>
  );
}
