import { ColumnDef } from "@tanstack/react-table";
import { Order } from "./types";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { DeleteOrderDialog } from "./components/delete-order-dialog";
import { toast } from "sonner";
import { deleteOrder } from "./api";

export const getColumns = (refreshData: () => void): ColumnDef<Order>[] => [
  {
    accessorKey: "orderNo",
    header: "Order No",
  },
  {
    accessorKey: "customerName",
    header: "Customer",
  },
  {
    accessorKey: "status",
    header: "Status",
    cell: ({ getValue }) => {
      const status = getValue<string>();
      return <Badge variant="outline">{status}</Badge>;
    },
  },
  {
    accessorKey: "amount",
    header: "Amount",
    cell: ({ getValue }) => `₹ ${getValue<number>().toFixed(2)}`,
  },
  {
    accessorKey: "createdAt",
    header: "Created At",
    cell: ({ getValue }) =>
      new Date(getValue<string>()).toLocaleDateString(),
  },
  {
    accessorKey: "updatedAt",
    header: "Updated At",
    cell: ({ getValue }) =>
      new Date(getValue<string>()).toLocaleDateString(),
  },
  {
    id: "actions",
    header: "Actions",
    cell: ({ row }) => (
      <div className="flex gap-2">
        <Button size="sm">Edit</Button>

        <DeleteOrderDialog
          onDelete={async () => {
            try {
              await deleteOrder(row.original.id); // Pass row ID
              toast.success("Order deleted successfully");
              refreshData(); // refresh table after deletion
            } catch (err) {
              toast.error("Failed to delete order");
            }
          }}
        />
      </div>
    ),
  },
];
